.transpBox{
    border-radius: 16px;
    width: 300px;
    margin: 30px;
    background-color: #ffffff;
    border: 5px solid lightgrey;
    opacity: 0.6;
    filter: alpha(opacity=60);
    left: 250px;
    position: absolute;
    -ms-flex-line-pack: center;
        align-content: center;
    top: 45px;
    left: 260px;
    margin-top: 100px;
}

.transpBox p{
    margin: 3%;
    font-weight: bold;
    color:black;
}

.transpBox h1 {
    margin: 5%;
    font-weight: bold;
    color:black;
}


