ul {
  font-weight: bold;
  background-color: #ffffff;
  border: 5px solid lightgrey;
  opacity: 0.8;
  filter: alpha(opacity=60);
    position: absolute;
    z-index: 100;
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: white;
    position: fixed;
    top: 0;
    width: 100%;
  }
  
  li {
    float: left;
  }
  
  li a {
    display: block;
    color: black;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
  }
  
  /* Change the link color to #111 (black) on hover */
  li a:hover {
    background-color: lightgrey;
  }

  .active {
    color: white;
  }

  .rightAlign{
      float : right;
  }


