body {
  font-family: 'Roboto';
}

.App{
  background-image:url(./Pictures/backgroundPic.jpg);
  width: 100%;
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  position: relative;
}
.profilePic {
  position: absolute;
  -ms-flex-line-pack: center;
      align-content: center;
  top: 8px;
  left: 16px;
  width:220px;
  margin: 10px;
  border:3px solid white;
  border-radius: 500px;
  -webkit-border-radius: 500px;
  -moz-border-radius: 500px;
  margin-top: 100px;
}
 
.bgPic{
  max-width: 100%;
  max-height: 1000px;
}

.infoPages .DIV{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align:  center;
      align-items:  center;
  -ms-flex-pack: center;
      justify-content: center;
  font-weight: bold;
  width : 100%;
  height: 100vh;
}

.infoPages div{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align:  center;
      align-items:  center;
  -ms-flex-pack: center;
      justify-content: center;
  font-weight: bold;
  width : 100%;
  height: 100vh;
}
h2{
  color: black;
  opacity: 0.9;
  font-size: 50px;
  background: #eee;
  padding: 8px 24px;
  border-radius: 16px;
}
.infoPages .DIV:nth-child(1){
  background-image: url("./Pictures/AboutMePic.jpg");
  background-size: cover;
  background-attachment: fixed;
}

.infoPages .DIV:nth-child(2){
  background-image: url("./Pictures/AboutMePic2.jpg");
  background-size: cover;
}
.infoPages .DIV:nth-child(3){
  background-image: url("./Pictures/backgroundPic.jpg");
  max-width: 100%;
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  position: relative;
}
.infoPages .DIV:nth-child(4){
  background-image: url("./Pictures/OSJM.jpg");
  background-size: contain ;
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
  background-color: wheat;
}
.infoPages div:nth-child(5){
  background-image: url("./Pictures/backgroundPic.jpg");
  max-width: 100%;
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  position: relative;
}
p{
  opacity: 0.8;
  font-size: 18px;
  margin: 10vh;
}










